.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-container {
  display: flex;
  flex: 1;
  min-height: calc(100vh - 80px);
  gap: 20px;
  padding: 20px;
}

.left-panel {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 400px;
}

.right-panel {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-width: 350px;
}

.fortune-section {
  flex: 0 0 auto;
}

.chat-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 400px;
}

.card-drawing-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 600px;
}

/* 반응형 디자인 */
@media (max-width: 1024px) {
  .app-container {
    flex-direction: column;
    padding: 15px;
    gap: 15px;
  }
  
  .left-panel,
  .right-panel {
    min-width: auto;
  }
  
  .left-panel {
    min-height: 350px;
  }
}

@media (max-width: 768px) {
  .app-container {
    padding: 10px;
    gap: 10px;
  }
  
  .left-panel {
    min-height: 250px;
  }
  
  .chat-section {
    min-height: 300px;
  }
}