.fortune-selector {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.selector-header {
  text-align: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.selector-header h3 {
  margin: 0 0 8px 0;
  color: white;
  font-size: 20px;
  font-weight: 600;
}

.selector-header p {
  margin: 0;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
}

.fortune-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.fortune-card {
  position: relative;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  overflow: hidden;
  aspect-ratio: 1;
  min-height: 140px;
}

.fortune-card:hover {
  transform: translateY(-3px);
  border-color: var(--card-color);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.fortune-card.selected {
  border-color: var(--card-color);
  background: linear-gradient(135deg, 
    rgba(255, 255, 255, 0.15) 0%, 
    rgba(255, 255, 255, 0.05) 100%
  );
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
}

.fortune-card.processing {
  pointer-events: none;
  opacity: 0.8;
}

.card-inner {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px;
  text-align: center;
}

.card-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

.card-icon span {
  font-size: 28px;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.processing-spinner {
  width: 28px;
  height: 28px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top: 3px solid var(--card-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.card-content h4 {
  margin: 0 0 8px 0;
  color: white;
  font-size: 14px;
  font-weight: 600;
}

.card-content p {
  margin: 0;
  color: rgba(255, 255, 255, 0.7);
  font-size: 11px;
  line-height: 1.3;
}

.card-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, 
    rgba(0, 0, 0, 0.7) 0%, 
    rgba(0, 0, 0, 0.5) 100%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease;
}

.fortune-card:hover .card-overlay {
  opacity: 1;
}

.fortune-card.selected .card-overlay {
  opacity: 1;
  background: linear-gradient(135deg, 
    rgba(0, 0, 0, 0.6) 0%, 
    rgba(0, 0, 0, 0.4) 100%
  );
}

.overlay-text {
  color: white;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.selector-footer {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  padding-top: 15px;
}

.info-text {
  text-align: center;
  margin-bottom: 15px;
}

.info-text p {
  margin: 4px 0;
  color: rgba(255, 255, 255, 0.7);
  font-size: 12px;
}

.processing-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.processing-animation {
  display: flex;
  gap: 8px;
  align-items: center;
}

.processing-animation span {
  font-size: 20px;
  animation: float 2s ease-in-out infinite;
}

.processing-animation span:nth-child(2) {
  animation-delay: 0.3s;
}

.processing-animation span:nth-child(3) {
  animation-delay: 0.6s;
}

.processing-info p {
  margin: 0;
  color: rgba(255, 255, 255, 0.9);
  font-size: 13px;
  text-align: center;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-5px);
  }
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .fortune-selector {
    padding: 15px;
  }
  
  .selector-header h3 {
    font-size: 18px;
  }
  
  .fortune-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
  }
  
  .fortune-card {
    min-height: 120px;
  }
  
  .card-inner {
    padding: 12px;
  }
  
  .card-icon {
    width: 40px;
    height: 40px;
    margin-bottom: 8px;
  }
  
  .card-icon span {
    font-size: 24px;
  }
  
  .processing-spinner {
    width: 24px;
    height: 24px;
    border-width: 2px;
  }
  
  .card-content h4 {
    font-size: 13px;
  }
  
  .card-content p {
    font-size: 10px;
  }
  
  .overlay-text {
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .fortune-grid {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  
  .fortune-card {
    aspect-ratio: 2.5;
    min-height: 100px;
  }
  
  .card-inner {
    flex-direction: row;
    text-align: left;
    padding: 15px;
  }
  
  .card-icon {
    margin-bottom: 0;
    margin-right: 15px;
    flex-shrink: 0;
  }
  
  .card-content {
    flex: 1;
  }
}

/* 사용자 정보 입력 폼 스타일 */
.user-form-container {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.form-header {
  text-align: center;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.form-header h3 {
  margin: 0 0 8px 0;
  color: white;
  font-size: 20px;
  font-weight: 600;
}

.form-header p {
  margin: 0;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
}

.user-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-row {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-row label {
  color: white;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-row input,
.form-row select,
.form-row textarea {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 8px;
  padding: 12px 15px;
  color: white;
  font-size: 14px;
  transition: all 0.3s ease;
}

.form-row input::placeholder,
.form-row textarea::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.form-row input:focus,
.form-row select:focus,
.form-row textarea:focus {
  outline: none;
  border-color: #4ECDC4;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 10px rgba(78, 205, 196, 0.3);
}

.form-row small {
  color: rgba(255, 255, 255, 0.6);
  font-size: 12px;
  font-style: italic;
}

.form-errors {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 15px;
  background: rgba(255, 59, 48, 0.1);
  border: 1px solid rgba(255, 59, 48, 0.3);
  border-radius: 8px;
}

.error-message {
  margin: 0;
  color: #ff6b6b;
  font-size: 13px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.form-actions {
  display: flex;
  gap: 15px;
  justify-content: flex-end;
  padding-top: 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-primary,
.btn-secondary {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 100px;
}

.btn-primary {
  background: linear-gradient(135deg, #4ECDC4 0%, #44A08D 100%);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(78, 205, 196, 0.3);
}

.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
}

/* 반응형 디자인 - 사용자 폼 */
@media (max-width: 768px) {
  .user-form-container {
    padding: 15px;
  }
  
  .form-header h3 {
    font-size: 18px;
  }
  
  .form-actions {
    flex-direction: column-reverse;
  }
  
  .btn-primary,
  .btn-secondary {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .form-row input,
  .form-row select,
  .form-row textarea {
    padding: 10px 12px;
    font-size: 16px; /* iOS 줌 방지 */
  }
}